.newFolder {
    color: white;
}

.folderCreation {
    margin: 10px;
}

.folderCreation_button {
    background-color: #cacaca;
    border-radius: 30px;
    border: 0;
    color: black;
    display: block;
    font-size: 12px;
    font-weight: bold;
    height: 60px;
    padding: 8px;
    text-align: center;
    text-decoration: none;
    width: 60px;
}

a.folderCreation_button {
    padding: 16px 8px 8px;
}

.folderCreation_button:hover {
    cursor: pointer;
}